
html,
body {
  height: 100%;
  min-height: 100%;
  position: relative;
  width: 100%;
}
html {
  font-size: 14px;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  overflow-x: hidden;
}
body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.45;
}
header {
  -webkit-transition: 0.3s padding cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: 0.3s padding cubic-bezier(0.25, 0.8, 0.25, 1);
  width: 100%;
  z-index: 1;
}
main {
  -webkit-transition: 0.3s padding cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: 0.3s padding cubic-bezier(0.25, 0.8, 0.25, 1);
  will-change: padding-left;
}
a {
  color: #03af8d;
}
::-ms-clear,
::-ms-reveal {
  display: none;
}
