.main-content {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: -webkit-transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1), -webkit-transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.main-content .feedback-form {
  -webkit-transform: translate(-$feedback-form-width, 0);
          transform: translate(-$feedback-form-width, 0);
}
.main-content--withForm {
  -webkit-transform: translate(660px, 0);
          transform: translate(660px, 0);
}
.main-content--withForm .feedback-form {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}
.main-content__map {
  position: absolute;
  width: 100%;
  left: 0;
  top: 40px;
  bottom: 0;
}
.main-content__toolbar .input-group--single-line {
  margin-top: 0;
}
.main-content__themes {
  position: absolute;
  width: 100%;
  z-index: 2000;
  top: 40px;
  bottom: 0;
  overflow: auto;
  background-color: #2ecc96;
  padding: 48px 24px;
}
@media (max-width: 1424px) {
.main-content__themes {
    padding-top: 24px;
}
}
.main-content__themes .container {
  max-width: 1424px;
}
.feedback-btn {
  position: absolute;
  left: 16px;
  bottom: 20px;
  z-index: 1000;
  margin: 0;
}


