.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 60px;
  background-color: #fff;
  border-bottom: 1px solid rgba(0,0,0,0.14);
  min-width: 1024px;
}
.header__title {
  position: absolute;
  line-height: 60px;
  left: 16px;
  top: 0;
}
@media only screen and (max-width: 599px) {
.header__title {
    left: 72px;
}
}
.header__title-text {
  display: inline;
  vertical-align: middle;
}
.header__logo {
  display: inline-block;
  vertical-align: middle;
  height: 36px;
  width: auto;
}
.header__login-button {
  position: absolute;
  right: 16px;
  top: 0;
  margin: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}
@media only screen and (max-width: 599px) {
.header__login-button {
    right: 72px;
}
}
.header-menu {
  text-align: center;
}
.header-menu__link {
  text-decoration: none;
  line-height: 60px !important;
  color: #03af8d;
}
.header-menu__link:first-child {
  margin-left: 24px;
}


